@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200;300;400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Zilla+Slab:wght@300;400;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
    scroll-behavior: smooth;
}

.App {
    min-height: 100vh;
    background-color: #6787e4;
    color: #fff;
    padding-top: 7.2vw;
    padding-bottom: 70px;
}

@media (max-width: 1000px) {
    .App {
        padding-top: 14vw;
    }
}

.pageTitle {
    text-transform: uppercase;
    display: flex;
    justify-content: center;
    font-family: "Montserrat", "sans-serif";
    font-size: 2.3vw;
    padding: 12px;
    font-weight: 300;
}

@media (max-width: 1000px) {
    .pageTitle {
        font-size: 5.5vw;
    }
}
.header {
    display: flex;
    width: 100%;
    position: fixed;
    cursor: pointer;
    justify-content: center;
    text-transform: uppercase;
    background-color: #5275e0;
    font-family: "Montserrat", sans-serif;
    font-size: 5vw;
    font-weight: 300;
    padding-bottom: 15px;
    box-shadow: 0 1px 5px #032896;
    z-index: 100;
    color: #fff;
}

@media (max-width: 1000px) {
    .header {
        padding-top: 15px;
        font-size: 6.5vw;
    }
}
.contentModal_landscape {
    object-fit: contain;
    border-radius: 10px;
}

.contentModal_portrait {
    object-fit: contain;
    border-radius: 10px;
    display: none;
}

.tagline {
    padding-bottom: 10px;
    align-self: center;
    font-size: 4vw;
    text-align: center;
}

.contentModal {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
    width: 100%;
    overflow-y: scroll;
    scrollbar-width: none;
}

.contentModal::-webkit-scrollbar {
    display: none;
}

.contentModal_about {
    padding: 10px;
    width: 95%;
    height: 90%;
    display: flex;
    flex-direction: column;
    font-family: 'Zilla Slab', serif;
    justify-content: space-evenly;
    font-weight: 400;
}

.contentModal_title {
    height: 12%;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    font-size: 5vw;
    font-weight: 500;
    margin-bottom: 20px;
}

.contentModal_description {
    display: flex;
    height: auto + 10%;
    max-height: 40%;
    overflow-y: scroll;
    padding: 10px 15px 4vw 15px;
    scrollbar-width: thin;
    box-shadow: inset 0 0 5px #042eaf;
    text-align: justify;
    border-radius: 10px;
    font-size: 1.05rem;
}

.contentModal_description::-webkit-scrollbar {
    display: none;
}

@media (min-width: 820px) {
    .contentModal_landscape {
        display: none;
    }
    .contentModal_portrait {
        display: flex;
        width: 35%;
    }
    .contentModal {
        flex-direction: row;
        justify-content: space-around;
        padding: 10px 0;
    }
    .contentModal_about {
        width: 55%;
        height: 100%;
        padding: 0;
        justify-content: space-around;
    }
    .tagline {
        font-size: 2.8vw;
    }
    .contentModal_title {
        font-weight: 400;
        font-size: 4vw;
    }
    .contentModal_description {
        font-size: 1.2rem;
        padding: 10px 15px 4vw 15px;
    }
}

@media (min-width: 1100px) {
    .tagline {
        font-size: 2vw;
    }
}
.carouselItem {
    display: flex;
    flex-direction: column;
    padding: 10px;
    object-fit: fill;
    margin-bottom: -20px;
}

.carouselItem_text {
    font-size: 1.1rem;
    word-wrap: break-word;
}

.carouselItem_img {
    border-radius: 10px;
    box-shadow: 0 0 6px #042eaf;
    object-fit: fill;
}
.media {
    display: flex;
    flex-direction: column;
    width: 200px;
    padding: 7px;
    margin: 5px 0;
    position: relative;
    font-family: "Montserrat", "sans-serif";
    background-color: #cdd9fe;
    color: #000;
    border-radius: 10px;
    transition: color 0.4s, background-color 0.4s;
    cursor: pointer;
}

.media:hover {
    color: #fff;
    background-color: #042eaf;
}

@media (max-width: 550px) {
    .media {
        width: 45%;
    }
}

.poster {
    border-radius: 10px;
}

.title {
    width: 100%;
    text-align: center;
    padding: 12px 0px;
    margin: 0;
    font-size: 1.2rem;
}

.subTitle {
    display: flex;
    justify-content: space-between;
    padding: 0px 3px 5px 3px;
}
.trending {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}
